@use "../abstracts/" as *;
.cart-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 0.4s;
  background-color: var(--color-accent);
  z-index: 999;
  visibility: hidden;
}

.cart {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: scroll;
  background-color: var(--color-100);
  z-index: 9999;
  padding: 1.5rem;
  transform: translateX(100%);
  transition: transform 0.4s;

  @include breakpoint(tablet) {
    width: 30vw;
    min-width: 450px;
  }

  &.show-cart {
    transform: translateX(0);
  }

  &.show-overlay{
    visibility: visible;
  }

  &__close-btn {
    margin: 0;
    padding: 0;
    & svg {
      width: 2.5rem;
      height: 2.5rem;
      stroke-width: 1.2;
    }
  }

  &__title{
    margin-top: var(--space-lg);
    margin-bottom: var(--space-xl);
  }




}
